.top-actions {
  display: flex;
  flex-grow: 1;
  align-items: stretch;
  justify-content: space-between;

  &__menu-opener {
    align-self: center;
    width: 18px;
    height: 8px;
    background-image: url(../images/header/menu-opener.svg);

    @include onLaptop {
      opacity: 0;
      cursor: none;
    }
  }

  &__menu-closer {
    align-self: center;
    width: 18px;
    height: 18px;
    background-position: center;
    background-image: url(../images/header/cross.svg);
  }
}
