.about {
  padding: 120px 0;

  @include onTablet {
    padding: 148px 0;
  }

  &__title {
    // font-family: "Poppins";
    font-weight: 600;
    font-size: 32px;
    color: $c-title-text;
    line-height: 150%;
    text-align: center;

    margin-bottom: 24px;

    @include onTablet {
      margin-bottom: 16px;
    }
  }

  &__description {
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    font-size: 22px;
    line-height: 150%;
    text-align: center;
    margin: 0 auto;

    color: $c-description-text;

    @include onTablet {
      font-size: 24px;
    }

    @include onLaptop {
      width: 728px;
    }
  }
}
