.vision {
  padding: 72px 0;
  background-color: $c-white;

  @include onTablet {
    padding: 200px 0;
    background-image: url(../images/Shapes.png);
    background-position: center;
    background-repeat: no-repeat;
  }

  &__title {
    margin-bottom: 24px;
    text-align: center;
    font-weight: 600;
    font-size: 44px;
    line-height: 120%;
    color: $c-title-text;

    @include onTablet {
      font-size: 52px;
    }

    @include onDesktop {
      line-height: 150%;
    }
  }

  &__subtitle {
    font-family: "Open Sans", sans-serif;
    margin-bottom: 56px;
    text-align: center;
    font-weight: 600;
    font-size: 22px;
    line-height: 150%;
    color: $c-description-text;

    @include onTablet {
      margin-bottom: 64px;
      font-size: 24px;
    }

    @include onDesktop {
      width: 558px;
      margin: 0 auto 64px;
    }
  }
}
